      height: 50px;
    }
    30%, 65% {
      margin-top: 20px;
      height: 30px;
    }
  }
  
  @-webkit-keyframes blink {
    0%, 10%, 85% {
      bottom: 0;
    }
    30%, 65% {
      bottom: 20px;
    }
    0% {
      transform: rotate(-0.08turn);
    }
    100% {
      transform: rotate(-1.08turn);
    }
